body {
  background-color: #ffeadb;
  max-width: 100%;
  font-family: Cormorant Garamond, serif;
  font-size: 18px;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #fff;
    background-color: #1a1a1a;
  }
}

@media (prefers-color-scheme: light) {
  body {
    color: #333;
    background-color: #ffeadb;
  }
}

body {
  font-size: 16px;
}

header {
  background-color: #ff9a76;
  border-left: .5em solid #000;
  justify-content: center;
  align-items: center;
  padding: .5em;
  display: flex;
}

.logga {
  box-sizing: border-box;
  border: 2px solid #ff0;
  border-radius: 40%;
  width: 3em;
  height: auto;
  margin-top: 2em;
  padding: 1em;
}

.container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gradient-heading, h1 {
  background: linear-gradient(90deg, #be2490, gray);
  color: #0000;
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 3em;
  font-weight: bold;
  transition: background .5s;
  display: inline-block;
}

nav {
  background-color: #0ff;
  border-radius: 25%;
  width: 250px;
  height: auto;
  font-size: 1.4em;
  display: none;
  position: absolute;
  top: .5em;
  right: 3em;
  box-shadow: 0 0 10px #000;
}

nav ul {
  text-align: center;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
}

nav li {
  width: 100%;
  padding: .8em 0;
}

nav a {
  color: #000;
  padding: 0 1em;
  text-decoration: none;
}

nav a:hover {
  color: #00f;
  text-decoration: underline;
}

.breadcrumbs {
  align-items: center;
  display: flex;
}

.breadcrumbs p {
  color: orange;
  text-shadow: 1px .5px .5px #000;
  font-size: 1.2em;
  font-weight: 500;
}

.breadcrumbs a {
  margin-left: .2em;
}

.menu-btn-open {
  border: 2px solid 40%;
  cursor: pointer;
  box-sizing: border-box;
  background-color: #0000;
  border-color: #ff0;
  flex-direction: column;
  align-items: center;
  padding: 15px 25px;
  display: flex;
  box-shadow: 1px 1px 1px #000;
}

.open {
  margin: 0 1em;
}

.menu-btn:hover {
  background-color: #ff4500;
  box-shadow: 0 0 5px #dcdcdc;
}

.bars {
  background: #fff;
  width: 40px;
  height: 3px;
  margin: 5px;
  display: block;
}

.menu-text {
  color: #fff;
  font-size: 1.2em;
}

.close {
  background-color: orange;
  border-radius: 50%;
  flex-direction: column;
  margin: 1em auto 0;
  padding: 10px 15px;
  display: flex;
}

.cross1 {
  width: 25px;
  position: relative;
  top: 5px;
  left: 3px;
  transform: rotate(45deg);
}

.cross2 {
  width: 25px;
  position: relative;
  top: -3px;
  left: 3px;
  transform: rotate(135deg);
}

@media (prefers-color-scheme: dark) {
  nav {
    background-color: #fafad2;
  }

  nav a {
    color: #4caf50;
  }

  nav a:hover {
    color: #388e3c;
  }

  .breadcrumbs a {
    color: #4caf50;
  }
}

.picture-container {
  max-width: 100%;
  height: auto;
}

img {
  gap: 2em;
  width: 50vw;
  margin: 0 auto 3em;
  display: flex;
}

.sass-evaluation {
  text-align: left;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.sass-evaluation p {
  width: 50%;
  line-height: 1.4em;
}

.sass-evaluation ul {
  margin-right: 15em;
}

footer {
  background-color: #ff9a76;
  justify-content: space-evenly;
  align-items: center;
  max-width: 100%;
  margin-top: 10em;
  display: flex;
  box-shadow: 1px 1px 1px 1px #000;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-20px);
  }

  60% {
    transform: translateY(-10px);
  }
}

.animation-img img {
  transition: transform .3s ease-in-out;
}

.animation-img:hover img {
  animation: 1s infinite bounce;
}

@keyframes blink-img {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }
}

.blinking-img {
  animation: 5s infinite blink-img;
}

@keyframes text-shadow {
  0% {
    text-shadow: 0 0 10px #00000080;
  }

  50% {
    text-shadow: 0 0 20px #000c;
  }

  100% {
    text-shadow: 0 0 10px #00000080;
  }
}

.text-animation {
  animation: 2s infinite text-shadow;
}

@keyframes slideintext {
  from {
    transform: translateX(-300px);
  }

  to {
    transform: translateX(0);
  }
}

.h2-animation {
  animation: 1s ease-in slideintext;
  animation-delay: 0;
  font-size: 2em;
}

.menu-btn-open {
  transition: transform .3s;
}

.menu-btn-open:hover {
  transform: translateY(-10px);
}

.barCavnas {
  justify-content: center;
  width: 100%;
  height: auto;
  display: flex;
}

.pieCanvas {
  justify-content: center;
  width: 100%;
  height: auto;
  margin-top: 5em;
  display: flex;
}

#map {
  height: 100vh;
  box-shadow: 1px 1px 1px #000;
}

.leaflet-control-geosearch *, .leaflet-control-geosearch :before, .leaflet-control-geosearch :after {
  box-sizing: border-box;
}

.leaflet-control-geosearch .leaflet-bar-part, .leaflet-control-geosearch a.leaflet-bar-part:before, .leaflet-control-geosearch a.leaflet-bar-part:after, .leaflet-control-geosearch a.leaflet-bar-part:before, .leaflet-control-geosearch a.leaflet-bar-part:after {
  display: none;
}

.leaflet-control-geosearch form {
  z-index: -1;
  background-color: #fff;
  background-clip: padding-box;
  border-radius: 0 4px 4px 0;
  height: auto;
  margin: 0;
  position: absolute;
  top: 0;
  left: 30px;
  box-shadow: 0 1px 5px #000000a6;
}

.leaflet-control-geosearch.active form {
  display: block;
}

.leaflet-control-geosearch form input {
  text-indent: 8px;
  width: 100%;
  min-width: 200px;
  height: 30px;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

.leaflet-control-geosearch .results {
  background: #fff;
}

.leaflet-control-geosearch .results > * {
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 0 8px;
  line-height: 24px;
  overflow: hidden;
}

.leaflet-control-geosearch .results > .active, .leaflet-control-geosearch .results > :hover {
  cursor: pointer;
  background-color: #f8f8f8;
  border-color: #c6c6c6;
}

